@import './_reset.css';

#App {
	width: 100%;
	height: 100svh;
	display: grid;
	place-content: center;
	font-size: 15px;
	font-family: 'Arial';
}

.start-game {
	display: flex;
}

.start-game form {
	width: 500px;
	height: 500px;
	display: grid;
	place-content: center;
	gap: 10px;
	border: 1px solid black;
}

h1 {
	font-weight: bold;
	text-align: center;
}

.name,
.level {
	border: 1px solid black;
	border-radius: 5px;
	padding: 3px 5px;
}

.error-value {
	border: 1px solid red;
}

.level {
	width: 50px;
	margin-left: 5px;
}

.button {
	width: fit-content;
	border-radius: 5px;
	background-color: rgb(0, 162, 0);
	color: white;
	padding: 5px 15px;
	&:hover {
		background-color: rgb(0, 138, 0);
	}
}

.game {
	width: 100%;
	position: relative;
	display: flex;
	justify-content: center;
}

.game-values {
	margin-left: 10px;
	display: flex;
	flex-direction: column;
	gap: 20px;
}

.speedometres {
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.score {
	display: flex;
	gap: 5px;
}

.score span {
	font-weight: bold;
}

h2 {
	font-weight: bold;
}

.scoreboard {
	width: 300px;
	max-height: 500px;
	.scoreboard-header {
		color: white;
		background-color: rgb(0, 138, 0);
	}
	.scoreboard-results {
		max-height: 463px;
		overflow-y: scroll;
		.row {
			&:nth-child(1) {
				background-color: gold;
			}
			&:nth-child(2) {
				background-color: #bcc6cc;
			}
			&:nth-child(3) {
				background-color: #cd7f32;
			}
		}
	}
}

.row {
	display: flex;
	justify-content: space-between;
	gap: 15px;
	&:not(:last-child) {
		border-bottom: 1px solid rgb(207, 207, 207);
	}
}

.cell {
	width: 90px;
	padding: 10px 10px;
	overflow-x: scroll;
	text-align: center;
	&:nth-child(1) {
		text-align: left;
	}
}

.popUp {
	position: absolute;
	top: -1%;
	width: 101%;
	height: 102%;
	display: grid;
	place-content: center;
	backdrop-filter: blur(5px);
	.container {
		padding: 20px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 20px;
		background-color: white;
		border: 1px solid rgba(0, 0, 0, 0.446);
		border-radius: 5px;
	}
	h2 {
		font-size: 25px;
	}
}
